.ProximaNova-Bold {
  font-family: 'ProximaNova-Bold', sans-serif;
}
.ProximaNova-Light {
  font-family: 'ProximaNova-Light', sans-serif;
}
.ProximaNova-Regular {
  font-family: 'ProximaNova-Regular', sans-serif;
}
.ProximaNova-Thin {
  font-family: 'ProximaNovaT-Thin', sans-serif;
}
h1 {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 56px;
  line-height: 72pt;
  color: #1d1e23;
}
h2 {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 32px;
  line-height: 40pt;
  color: #1d1e23;
}
h3 {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 28px;
  line-height: 40pt;
  color: #1d1e23;
}
h4 {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 24px;
  line-height: 32pt;
  color: #1d1e23;
}
h5 {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 18px;
  line-height: 24pt;
  color: #1d1e23;
}
h6 {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 14px;
  line-height: 24pt;
  color: #1d1e23;
}
.main-text {
  font-family: 'ProximaNova-Light', sans-serif;
  font-size: 18px;
  line-height: 28pt;
  color: #2b343c;
}
.text {
  font-family: 'ProximaNova-Regular', sans-serif;
  font-size: 16px;
  line-height: 24pt;
  color: #2b343c;
}
.headers-line {
  width: 70px;
  height: 2px;
  border-bottom: 2px solid #f52d42;
}
.bg-color-grey {
  background-color: #ebeef1;
}
.site-transition {
  transition: all .5s ease;
}
.container {
  min-width: 1024px;
}
.we-headers {
  margin-top: 150px;
}
.we-main-text {
  margin-top: 35px;
}
.half-bordered {
  border: 10px solid #f52d42;
  border-bottom: none;
  height: 100px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.arrow {
  background-image: url(../img/sprites/png/png-sprite.png);
  background-position: 0px 0px;
  width: 32px;
  height: 102px;
  position: relative;
  left: calc(50% - 16px);
  bottom: 51px;
}
.header {
  background-color: #ffffff;
  height: 75px;
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
}
@media (max-width: 1024px) {
  .header {
    width: 1024px;
  }
}
.header__logo {
  display: inline-block;
  margin-left: 60px;
  width: 243px;
  height: 37px;
}
@media (max-width: 1150px) {
  .header__logo {
    margin-left: 40px;
  }
}
.header__menu {
  display: inline-block;
  list-style-type: none;
}
.header__menu-item {
  display: inline-block;
  margin-right: 80px;
}
@media (max-width: 1150px) {
  .header__menu-item {
    margin-right: 40px;
  }
}
.header__menu-item:last-child {
  margin-right: 0;
}
.header__menu-link {
  font-family: 'ProximaNova-Bold', sans-serif;
  color: #666666;
  font-size: 14px;
  line-height: 75px;
  text-decoration: underline;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all .5s ease;
}
.header__menu-link:hover {
  color: #f52d42;
}
.header__btn {
  font-family: 'ProximaNova-Bold', sans-serif;
  font-size: 14px;
  color: #000000;
  height: 35px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  line-height: 30px;
  text-transform: uppercase;
  border: 2px solid #000000;
  background-color: #ffffff;
  outline: none;
  margin-right: 60px;
  margin-top: 20px;
  transition: all .5s ease;
}
@media (max-width: 1150px) {
  .header__btn {
    margin-right: 40px;
  }
}
.header__btn:hover {
  color: #f52d42;
  border: 2px solid #f52d42;
}
.footer {
  background-color: #ffffff;
  height: 70px;
  position: fixed;
  bottom: 290px;
  left: calc(50% - 285px);
  z-index: 10;
  width: 570px;
}
@media (max-height: 850px) {
  .footer {
    bottom: 250px;
  }
}
@media (max-height: 850px) {
  .footer {
    bottom: 200px;
  }
}
@media (max-height: 750px) {
  .footer {
    bottom: 150px;
  }
}
.footer__menu {
  display: inline-block;
  list-style-type: none;
  padding-left: 70px;
  padding-right: 70px;
}
.footer__menu-item {
  display: inline-block;
  margin-right: 48px;
}
.footer__menu-item:last-child {
  margin-right: 0;
}
.footer__menu-link {
  font-family: 'ProximaNova-Regular', sans-serif;
  color: #666666;
  font-size: 13px;
  line-height: 75px;
  letter-spacing: 2px;
  text-decoration: underline;
  text-transform: uppercase;
  transition: all .5s ease;
}
.footer__menu-link:hover {
  color: #f52d42;
}
